@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800&display=swap');

body {
  font-family: 'Inter', sans-serif;
}

.gradient-text {
  background: linear-gradient(135deg, #a855f7 0%, #3b82f6 50%, #06b6d4 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.gradient-border {
  position: relative;
  background: #121530;
  border-radius: 1rem;
}

.gradient-border::before {
  content: '';
  position: absolute;
  inset: 0;
  border-radius: 1rem;
  padding: 2px;
  background: linear-gradient(135deg, #a855f7, #3b82f6, #06b6d4);
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
  opacity: 0.5;
  transition: opacity 0.3s;
}

.gradient-border:hover::before {
  opacity: 1;
}

.blob {
  position: absolute;
  border-radius: 50%;
  filter: blur(80px);
  mix-blend-mode: screen;
  animation: blob 7s infinite;
  opacity: 0.3;
}

.blob-purple {
  background: #a855f7;
  top: 10%;
  left: 10%;
  width: 300px;
  height: 300px;
  animation-delay: 0s;
}

.blob-blue {
  background: #3b82f6;
  top: 50%;
  right: 10%;
  width: 350px;
  height: 350px;
  animation-delay: 2s;
}

.blob-cyan {
  background: #06b6d4;
  bottom: 10%;
  left: 50%;
  width: 280px;
  height: 280px;
  animation-delay: 4s;
}

.glass {
  background: rgba(18, 21, 48, 0.7);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(168, 85, 247, 0.1);
}

.hover-lift {
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.hover-lift:hover {
  transform: translateY(-8px);
}

.skill-icon {
  transition: all 0.3s ease;
}

.skill-card:hover .skill-icon {
  transform: scale(1.2) rotate(5deg);
}

.nav-link {
  position: relative;
  overflow: hidden;
}

.nav-link::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 0;
  height: 2px;
  background: linear-gradient(90deg, #a855f7, #3b82f6);
  transition: all 0.3s;
  transform: translateX(-50%);
}

.nav-link:hover::after {
  width: 80%;
}

.project-card {
  position: relative;
  overflow: hidden;
}

.project-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(168, 85, 247, 0.1), transparent);
  transition: left 0.5s;
}

.project-card:hover::before {
  left: 100%;
}

.timeline-dot {
  position: relative;
}

.timeline-dot::after {
  content: '';
  position: absolute;
  inset: -8px;
  border-radius: 50%;
  border: 2px solid #a855f7;
  opacity: 0;
  animation: pulse-ring 2s infinite;
}

.filter-btn.active {
  background: linear-gradient(135deg, #a855f7, #3b82f6);
  color: white;
  border-color: transparent;
}

.social-icon {
  position: relative;
  overflow: hidden;
}

.social-icon::before {
  content: '';
  position: absolute;
  inset: 0;
  background: linear-gradient(135deg, #a855f7, #3b82f6);
  opacity: 0;
  transition: opacity 0.3s;
  border-radius: 50%;
}

.social-icon:hover::before {
  opacity: 0.2;
}

.mesh-gradient {
  background: 
    radial-gradient(at 27% 37%, rgba(168, 85, 247, 0.15) 0px, transparent 50%),
    radial-gradient(at 97% 21%, rgba(59, 130, 246, 0.15) 0px, transparent 50%),
    radial-gradient(at 52% 99%, rgba(6, 182, 212, 0.15) 0px, transparent 50%),
    radial-gradient(at 10% 29%, rgba(168, 85, 247, 0.15) 0px, transparent 50%),
    radial-gradient(at 97% 96%, rgba(59, 130, 246, 0.15) 0px, transparent 50%),
    radial-gradient(at 33% 50%, rgba(6, 182, 212, 0.15) 0px, transparent 50%);
}

/* Lanyard Card Effects */
.lanyard-card {
  transform-style: preserve-3d;
  box-shadow: 
    0 25px 50px rgba(0, 0, 0, 0.4),
    0 10px 20px rgba(168, 85, 247, 0.2),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
  transition: all 0.6s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.lanyard-card:hover {
  box-shadow: 
    0 35px 70px rgba(0, 0, 0, 0.5),
    0 15px 30px rgba(168, 85, 247, 0.3),
    inset 0 1px 0 rgba(255, 255, 255, 0.15);
  transform: perspective(1000px) rotateX(-5deg) rotateY(5deg) translateZ(20px) !important;
}

/* Shine Animation */
.lanyard-shine {
  animation: shine 3s ease-in-out infinite;
}

@keyframes shine {
  0% {
    transform: translateX(-100%) skewX(-12deg);
  }
  50% {
    transform: translateX(100%) skewX(-12deg);
  }
  100% {
    transform: translateX(100%) skewX(-12deg);
  }
}

.lanyard-card:hover .lanyard-shine {
  animation-duration: 1.5s;
}

/* Perspective container */
.perspective-1000 {
  perspective: 1000px;
}